import InputArea from "../../plugin/components/InputArea";

export default {
  name: "ip_blacklist",
  components: {
    InputArea
  },
  data() {
    return {
      disabled: false,
      search_ip: "",
      currentPage: 1,
      pageSize: 10,
      total: 0,
      dialogTitle: "",
      formLabelWidth: "120px",
      dialogFormVisible: false,
      tableData: [],
      form: {
        forbid_ip: "",
        type: "1",
        remark: ""
      }
    };
  },
  filters: {
    formatStatus: function(val) {
      return val === 1 ? "解禁" : "封号";
    }
  },
  methods: {
    resetForm() {
      this.form = {
        id: null,
        forbid_ip: "",
        type: "1",
        remark: ""
      };
    },

    getBlackList() {
      this.$http
        .get("v1/backend/sys-conf/ip-blacklist", {
          params: {
            page: this.currentPage,
            limit: this.pageSize
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.tableData = res.data.data;
            this.total = res.data.total;
          }
        });
    },
    searchData() {
      this.$http
        .get("v1/backend/sys-conf/ip-blacklist", {
          params: {
            page: this.currentPage,
            limit: this.pageSize,
            ip: this.search_ip
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.tableData = res.data.data;
            this.total = res.data.total;
          }
        });
    },
    addBlackList() {
      if (!this.form.id) {
        let data = {
          ip: this.form.forbid_ip,
          op_type: Number(this.form.type),
          remarks: this.form.remark
        };
        this.$http.post("v1/backend/sys-conf/ip-blacklist", data).then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.dialogFormVisible = false;
            this.getBlackList();
            this.$message({
              type: "success",
              message: res.data.msg
            });
          }
        });
      } else {
        let data = {
          id: this.form.id,
          ip: this.form.forbid_ip,
          op_type: Number(this.form.type),
          remarks: this.form.remark
        };
        this.$http.post("v1/backend/sys-conf/ip-blacklist", data).then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.dialogFormVisible = false;
            this.getBlackList();
            this.$message({
              type: "success",
              message: res.data.msg
            });
          }
        });
      }
    },
    openAddDialog() {
      this.resetForm();
      this.disabled = false
      this.dialogTitle = "添加控制名单";
      this.dialogFormVisible = true;
    },
    handleEdit(row) {
      console.log(row);
      this.dialogTitle = "编辑";
      this.dialogFormVisible = true;
      this.form.id = row.id;
      this.form.forbid_ip = row.lock_data;
      this.form.type = String(row.lock_status);
      this.form.remark = row.memo;
    },
    change() {
      if (this.form.id) {
        this.disabled = true;
      }
    },
    updateStatus(row) {
      console.log(row);
      let data = {
        id: row.id,
        lock_status: row.lock_status === 0 ? 1 : 0
      };
      this.$http.put("v1/backend/sys-conf/ip-blacklist", data).then(res => {
        console.log(res);
        if (res.data.code === 200) {
          this.getBlackList();
          this.$message({
            type: "success",
            message: res.data.msg
          });
        }
      });
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.getBlackList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getBlackList();
    }
  },
  mounted() {
    this.getBlackList();
  }
};



